(function () {
    'use strict';
    var angular = require('angular');
    require('./save-exit.component.scss');

    var saveExit = angular.module('app').component('saveExit', {
        template: require('./save-exit.component.html'),
        controller: ['$anchorScroll', 'appState', 'analyticService', function ($anchorScroll, appState, analyticService) {
            var vm = this;

            vm.$onInit = function () {
                vm.isStaffJourney = appState.isStaffJourney();
                var appData = appState.getApplicationData() || {};
                appData = appData.primaryApplicant || {};
                var dipDetails = appData.dipDetails;
                if (dipDetails) {
                    vm.dipArn = dipDetails.dipArn;
                }
                analyticService.trackPageLoad(analyticService.pageList.savedApplication);
                appState.clearAll();
                $anchorScroll();
            };

            vm.exit = function () {
                // analyticService.buttonList.buttonClick.event_content = "exit";
                // analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                // $state.go("app.app-retrieval");
                window.location.href = "http://www.hsbc.co.in/";
            }

        }]
    });
    module.exports = saveExit.name;
})();



